(function () {
  'use strict';

  angular
    .module('angularGulp')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr, $resource, $scope, $rootScope) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = "";
    vm.searchText = "chair";
    vm.creationDate = 1517910008806;
    vm.showToastr = showToastr;
    vm.fetchSearch = fetchSearch;
    vm.chipsObj=["peter" , "nature Sky ", "school" , "cars" , "library", "Mobile" , "Camaro", "lamborghini" , "Oil Petrol"];

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = "rubberBand";
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = "";
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
 
    // , Secret: "6fe1e556757f454e",

    var FlickrAuth = { api_key: "e550f84233503cd16cb4815313c3f41d", format: "json", text: vm.searchText, nojsoncallback: 1 };
    var Search = $resource("https://api.flickr.com/services/rest/?method=flickr.photos.search");

    function fetchSearch(text){
      if (text) {
        FlickrAuth.text = text;
      vm.searchText=text;} 
      Search.get(FlickrAuth).$promise.then(function(result) {
          // success
          vm.items=[];
          vm.items = result.photos;
          console.log(vm.items.photo[0].title);
        }, function(error) {
          // fail
          console.log(error);
        });
      }
      fetchSearch();

    $rootScope.$on("searchFnEmit", function(event, data) {
      FlickrAuth.text = data;
      fetchSearch();
    });

  }
})();

